@import "variables";
body {
 margin: 0;
 padding: 0;
}

/**HEADER-NAVIGATION STYLING**/

.nav_control {
 display: none;
}
.head_nav {
 background-color: $mybackgroundcolor;
 display: flex;
 align-items: flex-end;
 position: fixed;
 width: 100%;
 z-index: 50;
 padding: 5px 0;
 h1 {
  @include commonfont;
  color: $mycolorwhite;
  text-shadow: 5px 4px rgba(129, 157, 158, 0.8);
  margin: 10px;
  z-index: 10;
 }
}

nav {
 width: 100%;
 display: flex;
}
.nav_ul {
 list-style-type: none;
 display: flex;

 width: 100%;
 justify-content: flex-end;

 li {
  margin: 0 20px;
  z-index: 10;

  cursor: pointer;
  font-weight: 500;
  font-family: $usedfontfamily;
  color: $mycolorwhite;
 }
 li:hover {
  text-shadow: 5px, 5px, grey;
  color: black;
 }
}
/**SECTION ONE STYLING*/
.background_img_cont {
 background-image: url("https://selfstoreandmore.co.uk/wp-content/uploads/2018/06/Internal-Self-Storage.jpg");
 background-repeat: no-repeat;
 background-size: cover;
 background-position: center center;
 top: 0;
 left: 0;
 width: 100%;
 height: 100vh;
 z-index: -1;
}
.page_overflow {
 width: 100%;
 height: 100%;
 background-color: rgba(85, 68, 158, 0.2);
 top: 0;
 position: absolute;
 bottom: 0;
 z-index: 1;
}
.sec_1 {
 height: 80vh;
 @include centereddisplay;
 article {
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 10;

  h2 {
   font-family: $usedfontfamily;
   color: $mycolorwhite;
   font-size: 4rem;
   margin-top: 180px;
  }
  button {
   width: 150px;
   height: 50px;
   color: $mycolorwhite;
   background-color: $mybackgroundcolor;
   font-weight: 900;
   border: none;
   border-radius: 5px;
  }
 }
}

/**LOG IN AND THE SIGN UP STYLING*/
.login_cont {
 background-color: rgb(12, 12, 12, 0.5);
 width: 100%;
 height: 100%;
 z-index: 15;
 position: absolute;

 @include centereddisplay;
}
.control_signin {
 background-color: transparent;
 @include centereddisplay;
 width: 100%;
 height: 100%;
 position: absolute;
}
.signin_cont {
 padding: 50px 40px;
 border-radius: 20px;
 z-index: 900;
 background-color: white;
 position: absolute;
}
.signin_h2 {
 font-family: $usedfontfamily;

 h2 {
  font-size: 1.4rem;
  color: $mybackgroundcolor;
 }
}
.signin_form {
 display: flex;
 flex-direction: column;

 label {
  font-family: $usedfontfamily;
  margin-top: 10px;
 }
}
.signin_input {
 border: 1px solid rgb(19, 108, 143, 0.6);
 height: 35px;
 outline: none;
 border-radius: 10px;
 font-family: $usedfontfamily;
 box-shadow: 2px 2px rgb(19, 108, 143, 0.6);
}
.sighin_submit_cont {
 @include centeredflexcolumn;
 padding: 10px;
 button {
  background-color: $mybackgroundcolor;
  color: white;
  margin-top: 15px;
  border: none;
  outline: none;
  width: 120px;
  height: 40px;
  font-size: 20px;
  font-weight: 700;
 }
 button:hover {
  color: $mybackgroundcolor;
  background-color: white;
 }
 p {
  font-family: $usedfontfamily;
  margin-top: 5px;
 }
}
.noaccount_cont {
 display: flex;
 justify-content: space-between;
 align-items: flex-start;

 button {
  border: 2px solid $mybackgroundcolor;
  background-color: transparent;
  height: 50px;
 }
}
.hidden {
 display: none;
}
/**SECTION 2 STYLING */
.sec2_form {
 display: flex;
 width: 100%;
}
.sec2_input,
.sec2_btn {
 margin: 20px 0;
 margin-left: 20px;
 padding: 10px 10px;
 width: 50%;
 border: 1px solid rgb(143, 141, 141, 0.4);
 outline: none;
 border-radius: 10px 0 0 10px;
 box-shadow: 3px 3px rgb(161, 173, 161);
 font-size: 1.1rem;
}
.sect2_btn2 {
 margin-left: 0;
 border-radius: 0 10px 10px 0;
 width: 120px;
 background-color: $mybackgroundcolor;
}
.facility_cont {
 display: flex;
 width: 100%;
 height: 80vh;
 @include centereddisplay;
}
.selffacility_link {
 @include centeredflexcolumn;
 height: 100%;
 text-decoration: none;
}
.selffacility_cont {
 display: flex;
 flex-direction: column;
 height: 100%;
 @include centereddisplay;
}
.sec2_img {
 width: 70%;
 height: 70%;
 border-radius: 20px;
}
.facility_title {
 color: black;
 font: {
  weight: 800;
  family: $usedfontfamily;
  size: 1.2rem;
 }
}
/**FOOTER STYLING STARTS HERE*/
footer {
 background-color: $mybackgroundcolor;
 height: 350px;
 display: flex;
 flex-direction: column;
}
.footer_sec {
 display: flex;

 justify-content: space-between;
 margin: 0 2%;
}
.ft_individual_cont {
 width: 25%;
}
.store_p {
 font-weight: 700;
 font-family: $usedfontfamily;
}
.googleplay_logo,
.applestore_logo {
 width: 150px;
 cursor: pointer;
}
.ft_header2 {
 font-family: $usedfontfamily;
 font-size: 1.5rem;
 text-align: left;
}
.ft_listitems {
 list-style-type: none;
 color: white;
 display: flex;
 flex-direction: column;

 text-align: left;
 font-family: $usedfontfamily;
 font-size: 1.1rem;
 font-weight: 400;
 letter-spacing: 4px;

 li {
  text-align: left;
  line-height: 30px;
  text-indent: -40px;
  cursor: pointer;
 }
}
.ft_connect_listitems {
 display: flex;
 flex-direction: row;
 padding: 0 5px;
}
.connect_cont {
 margin: 0 5px;
 cursor: pointer;
}
/***SERVICES STYLING STARTS HERE*/
.sevice_title {
 color: $mybackgroundcolor;
 margin: 10px 30px;
}
.service_sec {
 padding: 0 30px;
 display: flex;
}

.sec_space2 {
 margin-top: 15%;
}
.self_storage_cont {
 display: flex;
 flex-direction: column;
 align-items: center;

 p {
  font: {
   family: $usedfontfamily;
   weight: 600;
   size: 1.2rem;
  }
 }
}
.self_storage_imgcont {
 width: 70%;
 height: 350px;
}
.self_storage_img {
 width: 100%;
 height: 100%;
 border-radius: 40px;
}
/** RESPONSIVENESS STYLING STARTS HERE**/

/** RESPONSIVE STYLING FOR SMALL MOBILE SCREEN SIZES < 480PX*/
@media (max-width: 480px) {
 /* CSS rules for small screens */
 .head_nav {
  display: none;
 }
 .nav_control {
  display: flex;
  width: 100%;
 }
 .custom_nav {
  background-color: $mybackgroundcolor;
  width: 100%;
  overflow: hidden;
  padding-right: 45px;
 }
 .navbrand_link {
  @include commonfont;
  color: $mycolorwhite;
  text-shadow: 5px 4px rgba(129, 157, 158, 0.8);
  text-decoration: none;
  font-size: 1.4rem;
 }

 .custom_btn {
  background-color: aqua;
  background-color: transparent;
  cursor: pointer;
  border: 1.5px solid rgb(0, 0, 0, 0.2);
  line-height: 1;
  padding: 0.25rem 0.75rem;
  margin: 0;
  border-radius: 5px;
 }
 .custom_offcanvas {
  background-color: aqua;
 }
 .sec_1 article h2 {
  margin-top: 0;
  font-size: 2rem;
 }
 .sec_1 article button {
  margin-top: 20px;
 }
 .sec2_form {
  flex-direction: column;
  display: flex;
 }
 .sec2_input,
 .sec2_btn {
  width: 90%;

  border-radius: 10px;
  outline: none;
  margin: 20px 0 20px 20px;
  padding: 10px;
  font-size: 1.1rem;
  box-shadow: 3px 3px #a1ada1;
 }
 .sect2_btn2 {
  width: 120px;
  margin-top: 0;
  align-self: center;
 }
 .facility_cont {
  width: 100%;
  height: 80vh;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 120px;
 }
 .sec2_img {
  width: 100%;
  height: 70%;
  border-radius: 20px;
 }
 .selffacility_cont {
  height: 80%;
 }
 .selffacility_link {
  height: 100%;
 }
 .sevice_title {
  margin-top: 120px;
 }
 .service_sec {
  flex-direction: column;
  margin-top: 30px;
 }
 .self_storage_imgcont {
  width: 90%;
  height: 350px;
 }
 footer {
  height: auto;
 }
 .footer_sec {
  flex-direction: column;
  align-items: center;
  height: auto;
 }
 .ft_individual_cont {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
 }
 .ft_header2 {
  text-align: center;
  font-weight: 900;
  font-size: 1.5rem;
  width: 100%;
 }
 .ft_listitems {
  width: 100%;
  text-align: center;
  letter-spacing: 4px;
 }
 .ft_listitems li {
  text-align: center;
  text-indent: 0;
  width: 100%;
 }
 .store_div {
  margin: 5px 0;
 }
 .ft_connect_cont {
  width: 100%;
  text-align: center;
 }
 .ft_connect_listitems {
  align-items: center;
  justify-content: center;
 }
}
